"use client";

import { useState } from "react";
import Button from "@/components/ui/button/Button";
import TextArea from "@/components/form/input/TextArea";
import Badge from "@/components/ui/badge/Badge";
import { useWod } from "./wodStore";

export default function WodPost() {
  const { selectedDate, getWodByDate, getCommentsByDate, addComment } = useWod();
  const [message, setMessage] = useState("");

  const wod = getWodByDate(selectedDate);
  const comments = getCommentsByDate(selectedDate);

  return (
    <div className="space-y-6">
      {/* WOD card */}
      <div className="rounded-2xl border border-gray-200 bg-white dark:border-gray-800 dark:bg-white/[0.03]">
        <div className="flex items-center justify-between border-b border-gray-200 px-6 py-4 dark:border-gray-800">
          <div className="flex items-center gap-3">
            <h2 className="text-lg font-medium text-gray-800 dark:text-white">
              WOD
            </h2>
            <Badge color="success">
              <span className="text-xs">{selectedDate}</span>
            </Badge>
          </div>
        </div>

        <div className="p-4 sm:p-6">
          {wod ? (
            <pre className="whitespace-pre-wrap text-sm text-gray-700 dark:text-gray-300">
              {wod.content}
            </pre>
          ) : (
            <p className="text-sm text-gray-500 dark:text-gray-400">
              No hay WOD cargado para este d√≠a (mock).
            </p>
          )}
        </div>
      </div>

      {/* Comments card */}
      <div className="rounded-2xl border border-gray-200 bg-white dark:border-gray-800 dark:bg-white/[0.03]">
        <div className="border-b border-gray-200 px-6 py-4 dark:border-gray-800">
          <h3 className="text-lg font-medium text-gray-800 dark:text-white">
            Comentarios
          </h3>
          <p className="text-sm text-gray-500 dark:text-gray-400">
            Comentarios del WOD {selectedDate}
          </p>
        </div>

        <div className="space-y-4 p-4 sm:p-6">
          {/* Add comment */}
          <div className="rounded-xl border border-gray-200 p-4 dark:border-gray-800">
            <TextArea
              rows={3}
              placeholder="Escribe un comentario..."
              value={message}
              onChange={(e) => setMessage(e.target.value)}
            />
            <div className="mt-3 flex justify-end">
              <Button
                variant="primary"
                onClick={() => {
                  addComment(selectedDate, message);
                  setMessage("");
                }}
              >
                Publicar
              </Button>
            </div>
          </div>

          {/* List */}
          <div className="space-y-3">
            {comments.length === 0 ? (
              <p className="text-sm text-gray-500 dark:text-gray-400">
                Todav√≠a no hay comentarios.
              </p>
            ) : (
              comments.map((c) => (
                <div
                  key={c.id}
                  className="rounded-xl border border-gray-200 p-4 dark:border-gray-800"
                >
                  <div className="flex items-center justify-between">
                    <p className="text-sm font-semibold text-gray-800 dark:text-white/90">
                      {c.author.name}
                    </p>
                    <span className="text-xs text-gray-500 dark:text-gray-400">
                      {new Date(c.createdAt).toLocaleString()}
                    </span>
                  </div>
                  <p className="mt-2 text-sm text-gray-700 dark:text-gray-300">
                    {c.message}
                  </p>
                </div>
              ))
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
